.advertisements{
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  
}

.advertisements > * {
  width: 50%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  row-gap: 1rem;
  padding: 2rem;
  flex-wrap: wrap;
  background-color: rgba(190, 227, 219, 0.2);
}

.ads-list{
  column-gap: 2rem;
  row-gap: 2rem;
  justify-content: center;
  width: 80%;
}

.add-adv > * {
  display: flex;
  justify-content: space-between;
  row-gap: 1rem;
  column-gap: 1rem;
  align-items: center;
  flex-direction: column;
  padding: .5rem;
  text-align: center;
}

li{
  list-style: none;
}



h3 {
  margin: 0;
  white-space: nowrap;
}

.add-adv input {
  width: 100%;
  border: 2px solid #555b6e;
  padding: 0.7rem;
  flex-grow: 1;
}

.price {
  display: flex;
}

.submit-button,
input[type='checkbox'],
label,
input[type='radio'],
select {
  cursor: pointer;
}

.big-field {
  width: 100%;
}

.small-field {
  width: 33%;
}

.middle-field {
  width: 50%;
}

.add-adv .submit-button{
  color: white;
  font-size: 1.2rem;
  border: none;
  background-color: #89B0AE;
}

.submit-button:disabled{
  background-color: #555b6e41;
}


input[name="typeAdd"]{
  display: none;
}

.switcher{
  border: 2px solid transparent;
}

input[name="typeAdd"]:checked+label{
  background-color: white;
  border: 2px solid #555b6e;
}



.ad-type{
  flex-direction: row;
  flex-wrap: wrap;
  
}

.ad-type h3{
  flex-grow: 1;
  
}

.ad-type label{
  flex-grow: 1;
  padding: .5rem;
  font-weight: bold;
  
}

input::placeholder {
  text-align: center;
}

select{
  border-radius: 0;
  border: 2px solid #555b6e;
  border-left: none;
  background-color: white;
}

.error-note{
  color: #e63946;
  font-size: smaller;
}





